import http from '@/api/http.js'
export default {
	data() {
		return {
			agreement:''
		};
	},
	onLoad(){
		this.getAgreement()
	},
	methods:{
		getAgreement(){
			
			http('/api/setting/userAgreement','post').then(res=>{
				this.agreement = res.data.data.userAgreement
				console.log(res)
			})
		}
	}
}
